@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background: #1a1a1c;
  color: #ffffff;
}

.coin-app{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;
}

.coin-search{
  margin-bottom: 64px;
  display:flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
}

.coin-text{
  margin-bottom: 32px;
  text-align: center;
}

.coin-input{
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #32e44a 0%,
  #18f235 48%, #00be19 100%);
  color: #141414;
  outline: none;
}

.coin-input::-webkit-input-placeholder{
  color: #141414;
}

.coin-input:-ms-input-placeholder{
  color: #141414;
}

.coin-input::-ms-input-placeholder{
  color: #141414;
}

.coin-input::placeholder{
  color: #141414;
}
.coin-container{
    display:flex;
    justify-content:center;
}

.coin-row {
    display:flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;

}

.coin{
    display:flex;
    align-items:center;
    padding-right: 24px;
    min-width: 300px;

}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin h1{
    font-size: 16px;
    width: 50px;
}

.coin-symbol{
    text-transform: uppercase;
}

.coin-data{
    display:flex;
    text-align:center;
    justify-content: space-between;
    width: 100%;
}

.coin-price{
    width: 110px;
    font-size: 18px;
    font-weight:bold;
}

.coin-volume{
    width: 200px;
}

.coin-marketcap{
    width: 240px;
}

.coin-percent{
    width: 80px;
}

.red{
    color: red;
}

.green{
    color: #18f235;
}
